let backgroundhome = "background-color: " + backgroundcolor + "; color: #fff;background-image:url(" + background + ");background-size:cover;background-position: center center;"

const Slides = ({ name, content, property, animation }) => (
  <div>
    {name
        ? <h2>{ name }</h2>
        : <span/>
    }
    {content
        ? <p className="small">{ content }</p>
        : <span/>
    }
  </div>
);

const mainlogo = {
  path: logo,
  x: "5%",
  y: "5%",
  w: "10%",
  type: 'cover'
}

const mainlogodark = {
  path: logodark,
  x: "5%",
  y: "5%",
  w: "10%",
  type: 'cover'
}

var bigTitle = {
  x: 0,
  y: 0,
  w: "100%",
  h: "100%",
  align: 'center',
  bold: true,
  fontSize: 48,
  color: 'FFFFFF'
}

var bigTitleBlack = {
  x: 0,
  y: 0,
  w: "100%",
  h: "100%",
  align: 'center',
  bold: true,
  fontSize: 48,
  color: '000000'
}

function App() {
  function PowerPoint(e) {
    e.stopPropagation();
    let pptx = new pptxgen();
    pptx.author = 'Bertrand Keller';
    pptx.company = 'Ministère de l’Économie, des Finances et de la Relance';
    pptx.addSection({ title: "Qui" });
    pptx.addSection({ title: "Quoi" });
    pptx.addSection({ title: "Pourquoi" });
    pptx.addSection({ title: "Comment" });


    pptx.defineSlideMaster({
      title: "MAIN",
      background: {
        path: background,
        color: backgroundcolor
      },
      objects: [{
        image: mainlogodark
      }, ],
    });

    pptx.defineSlideMaster({
      title: "CONTENT",
      background: {
        color: "FFFFFF"
      },
      objects: [{
        image: mainlogo
      }, ],
      slideNumber: {
        x: 0.3,
        y: "90%"
      },
    });

    let slide = pptx.addSlide({ masterName: "MAIN", sectionTitle: "Qui" });

		slide.addText(maintitle, {
			x: "5%",
			y: "40%",
			w: "50%",
      h: 1,
      bold: true,
      fontSize: 32,
      fontFace: 'Segoe UI',
			color: 'FFFFFF',
    });

    slide.addText("- Bertrand Keller", {
			x: "5%",
			y: "80%",
			w: "50%",
      h: 1,
      bold: true,
      fontSize: 22,
      fontFace: 'Segoe UI',
			color: 'FFFFFF',
		});

    slide.addText("@bertrandkeller", {
			x: "65%",
			y: "80%",
			w: "50%",
      h: 1,
      bold: true,
      fontSize: 22,
      fontFace: 'Segoe UI',
			color: 'FFFFFF',
    });

    for (const groups of content) {
      let slides = groups.slides
      pptx.addSection({ title: groups.name });
      if (slides) {
        var varslide = pptx.addSlide({ masterName: "MAIN", sectionTitle: groups.name });
        varslide.addText(groups.name, bigTitle);
      }
      else {
        var varslidee = pptx.addSlide({ masterName: "CONTENT", sectionTitle: groups.name });
        varslidee.addText(groups.name, bigTitleBlack);
        if (groups.content)   { varslidee.addText(groups.content, { x: "5%", y: "75%", w: 9.0, fontSize: 18, align: 'center', lineSpacing: 10 }); }
      }
      if (slides) {
        slides.forEach((text) => {
          var varslideinside = pptx.addSlide({ masterName: "CONTENT", sectionTitle: groups.name });
          var fontsize = 18
          var align = 'left'
          if (text.content) {
            if (text.content.length > 200) {
              fontsize = 12
            }
            if (text.content.length < 60) {
              fontsize = 26
              align = 'center'
            }
            if (text.content.length < 40) {
              fontsize = 32
            }
          }
          if (text.name)                                    { varslideinside.addText(text.name, { x: "20%", y: "15%", fontSize: 32, bold: true }); }
          if (text.content && !text.picto)                  { varslideinside.addText(text.content, { x: "5%", y: "55%", w: 9.0, fontSize: fontsize, align: align }); }
          if (text.picto   && !text.content)                { varslideinside.addImage({ x: "45%", y: "45%", w: 1.0, h: 1.0, path: text.picto }); }
          if (text.content && text.picto)                   { varslideinside.addText(text.content, { x: "5%", y: "75%", w: 9.0, fontSize: 22, align: 'center' }); varslideinside.addImage({ x: "45%", y: "45%", w: 1.0, h: 1.0, path: text.picto }); }
          if (text.illustration)                            { varslideinside.addImage({ x: "10%", y: "30%", w: 8.0, h: 3.5, path: text.illustration }); }
          if (text.illustration && text.illustrationpptx)   { varslideinside.addImage({ x: "10%", y: "30%", w: 8.0, h: 3.5, path: text.illustrationpptx }); }
          if (text.screenshot)                              { varslideinside.addImage({ x: "0%", y: "10%", w: 3.5, h: 3.5, path: text.screenshot, sizing: { type: 'contain', w: "100%", h: "80%" } }); }
        });
      }
    }

    // Il y a 2 ans, je présentais une conférence sur l’Éco-conception : 70% des interfaces que nous réalisons ne sont pas utilisées. Accessibilité
    // Un monde numérisé ? Imaginons, vous voulez avoir une idée de votre taux de conformité, alors… blabla… blabla…… (schéma de fonctionnement)
    // Qu’est-ce qu’un taux de conformité (RGAA) petit tour du côté de ce que demande la loi ? Pourquoi c’est important ? Qu'est-ce qui est révélé ? <== Engagement de moyen
    // Comment ça devrait être (schéma) ? Les ponts bloquants…
    // Est-ce qu'on peut simplifier tout ça ? A grane échelle ?

    // Parler du cycle de l’accessibilité : une roue qui tourne ? Un jeu de plateau ? Un jeu par étape.



		pptx.writeFile({ fileName: "bertrand-keller.pptx" });
	}

  //console.log(content.filter(item => !!item.slides).map( item => console.log(item)))

  let array = []

  content.forEach((group) => {
    var absolute = ""
    var property = ""
    var content = ""
    var slidedark = ""
    if (group.slides)               { property = "background-color:" + backgroundcolor + ";color:#fff;background-image:url(" + background + ");background-size:cover;background-position:center center;"; slidedark = "slidedark"  }
    if (group.picto)                { property = "background-color:" + backgroundcolor + ";color:#fff;background-image:url(" + group.picto + ");background-size:8%;background-position:center 20%;"; absolute = "absolute"; slidedark = "slidedark" }
    if (group.content)              { content = group.content }
    array.push({name: group.name, content: content, property: property, absolute: absolute, slidedark: slidedark })
    if (group.slides) {
      group.slides.forEach((groupinside) => {
        var nameslide = ""
        var propertyslide = ""
        var absoluteslide = ""
        var contentslide = ""
        var animationslide = ""
        var slidedarkslide = ""
        if (groupinside.name)        { nameslide = groupinside.name }
        if (groupinside.picto)       { propertyslide  = "background-image:url(" + groupinside.picto + ");background-size:15%;background-position: center center;" ; absoluteslide = "absolute"  }
        if (groupinside.illustration){ propertyslide  = "background-image:url(" + groupinside.illustration + ");background-position: center center;background-size: auto 60%" ; absoluteslide = "absolute" }
        if (groupinside.screenshot)  { propertyslide  = "background-image:url(" + groupinside.screenshot + ");background-position: center center;background-size: auto 90%" ; absoluteslide = "absolute" }
        if (groupinside.animation)   { animationslide = groupinside.animation ; absoluteslide = "absolute" }
        if (groupinside.content)     { contentslide   = groupinside.content }
        array.push({name: nameslide, content: contentslide, animation: animationslide, property: propertyslide, absolute: absoluteslide, slidedark: slidedarkslide })
      })
    }
  })

  console.log(array)

	return (
    <main className="container">
      {logo
        ? <img className="logo" src={ logo } width="100" alt="" />
        : <span/>
      }
      {logodark
        ? <img className="logo dark" src={ logodark } width="100" alt="" />
        : <span/>
      }
      <div data-body-class="slidedark" data-body-style={ backgroundhome }>
        <h2>{ maintitle }</h2>
        <button id="stopPropagation" type="button" className="btn primary small" onClick={(_ev) => PowerPoint(_ev)}>Télécharger la présentation PowerPoint</button>
      </div>
      {array.map((group) => (
          <div data-body-class={group.slidedark} data-absolute={group.absolute} data-body-style={group.property}>
          <Slides
            property={`${group.property}`}
            name={`${group.name}`}
            content={`${group.content}`}
            animation={`${group.animation}`}
          />
          </div>
        ))}
    </main>
	);
}

export default App;
